import { useState, useEffect, useContext } from "react";
import CreateAndEditModal from "@/Components/CreateAndEditModal/CreateAndEditModal";
import HealthConditionModal from "@/Components/HealthConditionModal/HealthConditionModal";
import LiveRateChart from "./LiveRateChart";
import { LuHeartPulse } from "react-icons/lu";
import { RiLungsLine } from "react-icons/ri";
import { Tooltip, Button } from "antd";
import { MdEdit } from "react-icons/md";
import EventCalender from "./EventCalender";
import { GaugeComponent } from "react-gauge-component";
import GaugeChart from "./Gauge";
import { getHealthScore } from "@/api/deviceReports";
import ls from "store2";
import { formatTimeToHoursAndMinutes } from "@/utils/helper";
import { CustomContext } from "@/Context/UseCustomContext";

export default function ElderlyProfile() {
  // const elderlyDetais = ls.get("elderly_details");
  const { sleepData, elderlyDetails } = useContext(CustomContext);

  const [isModalOpen, setIsModalOpen] = useState(false);
  const labelMapping = {
    合格: "Pass",
    不合格: "Fail",
    睡眠时长偏少: "Sleep duration is too short",
  };
  const [comments, setComments] = useState([]);
  const categorizeData = (data) => {
    const categorizedData = {};
    data?.forEach((item) => {
      const { category } = item;
      if (!categorizedData[category]) {
        categorizedData[category] = [];
      }
      categorizedData[category].push(item);
    });
    return categorizedData;
  };
  useEffect(() => {
    setComments(categorizeData(elderlyDetails.comments));
  }, [elderlyDetails]);

  const getInitials = (name = "") => {
    const splitName = name?.split(" ");
    return `${splitName[0]?.[0]?.toUpperCase() || ""}${
      splitName[1]?.[0]?.toUpperCase() || ""
    }`;
  };
  return (
    <>
      <div className="flex flex-col gap-5">
        <div className="overflow-hidden relative flex flex-col gap-0 !text-text-primary rounded-2xl ">
          {/* <div
            id="GlowShapeForStatus"
            className={`absolute z-0 top-[-100px] scale-125 left-0 w-full h-[180px] ${
              ElderlyDetailsData.status === "critical"
                ? "critical"
                : ElderlyDetailsData.status === "warning"
                ? "warning"
                : ElderlyDetailsData.status === "problem"
                ? "problem"
                : "good"
            }`}
            style={{
              filter: "blur(100px)",
            }}
          ></div> */}

          <div
            id="profileHeader"
            className="z-50 flex flex-col items-start justify-start gap-4 rounded-t-2xl p-6 w-full bg-white"
          >
            <div className="flex items-start gap-4 w-full">
              <span className="relative flex h-[80px] w-[80px]">
                {ElderlyDetailsData.status !== "normal" && (
                  <span
                    className={`animate-ping absolute inline-flex h-full w-full rounded-full ${
                      ElderlyDetailsData.status === "critical"
                        ? "bg-red-400"
                        : ElderlyDetailsData.status === "warning"
                        ? "bg-orange-400"
                        : ElderlyDetailsData.status === "normal"
                        ? "bg-none"
                        : "bg-blue-400"
                    } opacity-75`}
                  ></span>
                )}
                <span
                  className={`relative rounded-full h-[80px] w-[80px] ${
                    ElderlyDetailsData.status === "critical"
                      ? "bg-red-500 border-red-500"
                      : ElderlyDetailsData.status === "warning"
                      ? "bg-orange-500 border-orange-500"
                      : ElderlyDetailsData.status === "normal"
                      ? "bg-green-500 border-green-500 !border-0"
                      : "bg-blue-500 border-blue-500"
                  } flex items-center justify-center overflow-hidden border-0 bg-[#80CAA7] font-semibold text-3xl text-white`}
                >
                  {getInitials(elderlyDetails?.name)}
                  {/* <img
                    className="w-full h-full object-cover overflow-hidden"
                    src="https://www.shutterstock.com/image-photo/elderly-hispanic-man-grey-hair-260nw-2511514837.jpg"
                    alt=""
                  /> */}
                </span>
                <div id="age" className="absolute -bottom-1 -right-1">
                  <p
                    className="text-sm text-text-white/70 px-1.5 py-1.5 rounded-full font-semibold leading-none border-2 border-white/20 border-inner"
                    style={{
                      backgroundColor: "white",
                      border: "1px solid #E0E0E0",
                    }}
                  >
                    {elderlyDetails?.age}
                  </p>
                </div>
              </span>
              <div id="elderlyNameAndAddress" className="pt-4">
                <h1 className="text-[24px] font-bold">
                  {elderlyDetails?.name}
                </h1>
                <div
                  id="addresandGoogleMapButton"
                  className="flex items-center gap-2 md:flex-row flex-col"
                >
                  <Tooltip title={"View on Google Map"}>
                    <a
                      href={`https://www.google.com/maps?q=${elderlyDetails.latitude},${elderlyDetails.longitude}`}
                      target="_blank"
                      className="cursor-pointer font-normal hover:text-text-primary transition-all duration-300 text-[17px] text-text-primary/80 flex items-center gap-1"
                    >
                      {elderlyDetails?.address}
                    </a>
                  </Tooltip>
                  {/* <div
                    id="dot"
                    className="!w-1 !h-1 bg-white/60 rounded-full"
                  ></div>
                  <p className="text-base text-white/70">
                   
                  </p> */}
                </div>
              </div>
            </div>
          </div>
          <div
            id="healthScoreGauge"
            className=" items-center justify-center flex flex-col bg-white rounded-b-2xl"
          >
            <div className="w-[340px] relative">
              <GaugeChart percentage={sleepData?.score} />
            </div>
          </div>
          <div className=" w-full grid grid-cols-2 gap-3 md:grid-cols-3 lg:grid-cols-3 mt-4">
            <HealthScoreItem
              score={sleepData?.ahi || "--"}
              label={"AHI Index"}
              status={
                labelMapping[sleepData?.evaluation?.ahi_unqualified_evaluation]
              }
            />
            <HealthScoreItem
              score={formatTimeToHoursAndMinutes(sleepData?.sleep_total_time)}
              label={"Sleep Duration"}
              status={
                labelMapping[sleepData?.evaluation?.sleep_duration_evaluation]
              }
            />
            <HealthScoreItem
              score={
                sleepData?.sleep_quality ? sleepData?.sleep_quality + "%" : "--"
              }
              label={"Deep Sleep (%)"}
              status={
                labelMapping[sleepData?.evaluation?.sleep_deep_ratio_evaluation]
              }
            />
            <HealthScoreItem
              score={
                sleepData?.leave_bed_count >= 0
                  ? sleepData?.leave_bed_count + "x"
                  : "--"
              }
              label={"Bed-Exit at Night"}
              status={
                labelMapping[sleepData?.evaluation?.sleep_leave_bed_evaluation]
              }
            />
            <HealthScoreItem
              score={
                sleepData?.breath_rate_vo?.avg
                  ? sleepData?.breath_rate_vo?.avg + " BPM"
                  : "--"
              }
              label={"Avg. Breath Rate"}
              status={
                sleepData?.breath_rate_vo?.avg
                  ? sleepData?.breath_rate_vo?.avg >= 12 &&
                    sleepData?.breath_rate_vo?.avg <= 30
                    ? "Pass"
                    : "Fail"
                  : "--"
              }
            />
            <HealthScoreItem
              score={
                sleepData?.heart_rate_vo?.avg
                  ? sleepData?.heart_rate_vo?.avg + " BPM"
                  : "--"
              }
              label={"Avg. Heart Rate"}
              status={
                sleepData?.heart_rate_vo
                  ? sleepData?.heart_rate_vo?.avg >= 60 &&
                    sleepData?.heart_rate_vo?.avg <= 100
                    ? "Pass"
                    : "Fail"
                  : "--"
              }
            />
          </div>
        </div>

        {/* <div className="w-full flex flex-col gap-4">
          <div className="w-full flex flex-col bg-[#6e77eb] pb-0 gap-4 justify-between rounded-xl pt-2">
            <div className="flex items-center justify-between px-4 text-white">
              <p className="text-base font-medium flex items-center gap-2">
                <LuHeartPulse size={20} /> Realtime Heart Rate{" "}
              </p>
              <h1 className="text-2xl font-bold">
                {data.heartRate[data.heartRate.length - 1]}{" "}
                <span className="text-sm text-white/70 font-normal">/BPM</span>
              </h1>
            </div>
            <LiveRateChart data={data} />
          </div>
          <div className="w-full flex flex-col bg-[#32c1c1] pb-0 gap-4 justify-between rounded-xl pt-2">
            <div className="flex items-center justify-between px-4 text-white">
              <p className="text-base font-medium flex items-center gap-2">
                <RiLungsLine size={20} /> Realtime Breath Rate{" "}
              </p>
              <h1 className="text-2xl font-bold">
                {data.breathRate[data.breathRate.length - 1]}{" "}
                <span className="text-sm text-white/70 font-normal">/BPM</span>
              </h1>
            </div>
            <LiveRateChart data={data} />
          </div>
        </div> */}

        <div
          id="elderlyDiseasesAllergies"
          className=" flex flex-col gap-6 bg-white rounded-2xl p-4"
        >
          <div className="flex items-center justify-between">
            <h1 className="text-lg font-bold text-text-primary leading-none flex items-center gap-2">
              Health Conditions
            </h1>
            <Tooltip title="Edit Health Conditions">
              <Button
                onClick={() => setIsModalOpen(true)}
                shape="circle"
                icon={<MdEdit />}
              />
            </Tooltip>
          </div>
          <div
            id="elderlyDiseasesAllergiesMedications"
            className="mt-3 flex flex-col gap-6"
          >
            <div id="elderlyDiseases" className="flex flex-col gap-2">
              <p className="text-base font-semibold text-[#7E60BF] leading-none flex items-center gap-2">
                <div
                  id="dot"
                  className="w-2 h-2 bg-[#7E60BF] rounded-full"
                ></div>{" "}
                Diseases
              </p>
              <ul className="list-none list-inside flex flex-wrap gap-2">
                {elderlyDetails?.diseases?.map((disease, index) => (
                  <li
                    key={index}
                    className="text-base font-medium p-1 px-3 bg-[#F5F3FF] rounded-lg"
                  >
                    {disease}
                  </li>
                ))}
              </ul>
            </div>
            {Object.keys(comments).map((category, index) => (
              <div
                key={index}
                id="elderlyDiseases"
                className="flex flex-col gap-2"
              >
                <p
                  className={`text-base font-semibold ${
                    category === "Allergy"
                      ? "text-[#4ca6cf]"
                      : category === "Disability"
                      ? "text-[#f37f13]"
                      : "text-[#0a0a2b]"
                  }  leading-none flex items-center gap-2 mb-2`}
                >
                  <div
                    id="dot"
                    className={`w-2 h-2 ${
                      category === "Allergy"
                        ? "bg-[#4ca6cf]"
                        : category === "Disability"
                        ? "bg-[#f37f13]"
                        : "bg-[#0a0a2b]"
                    }  rounded-full`}
                  ></div>{" "}
                  {category}
                </p>
                <ul className="list-none list-inside flex flex-wrap gap-2">
                  {Array.isArray(comments[category]) ? (
                    comments[category].map((comment, index) => (
                      <li
                        key={index}
                        className={`text-base font-medium p-1 px-3 ${
                          category === "Allergy"
                            ? "bg-[#4ca6cf]/10"
                            : category === "Disability"
                            ? "bg-[#f37f13]/10"
                            : "bg-[#0a0a2b]/10"
                        } rounded-lg`}
                      >
                        {comment.comment}
                      </li>
                    ))
                  ) : (
                    <p>No Comments</p>
                  )}
                </ul>
              </div>
            ))}
          </div>
          {/* <div id="elderlyAllergies" className="flex flex-col gap-2">
            <p className="text-base font-semibold text-[#798645] leading-none flex items-center gap-2">
              <div id="dot" className="w-2 h-2 bg-[#798645] rounded-full"></div>{" "}
              Allergies
            </p>
            <ul className="list-none list-inside flex flex-wrap gap-2">
              {elderlyDetails?.allergies?.length > 0 ? (
                elderlyDetails?.allergies?.map((allergy, index) => (
                  <li
                    key={index}
                    className="text-base font-medium p-1 px-3 bg-[#E8F9E9] rounded-lg"
                  >
                    {allergy}
                  </li>
                ))
              ) : (
                <p className="text-base font-medium p-1 px-3 bg-[#ffffff] rounded-lg text-center mt-2 text-text-primary/50">
                  No Allergies
                </p>
              )}
            </ul>
          </div> */}
        </div>
        <EventCalender />
      </div>
      <HealthConditionModal
        modalOpen={isModalOpen}
        setModalOpen={setIsModalOpen}
        mode="edit"
        diseases={elderlyDetails?.diseases}
        custom_condition={elderlyDetails?.comments}
        elderly_id={elderlyDetails?._id}
      />
    </>
  );
}
export const HealthScoreItem = ({ score, label, status }) => {
  return (
    <div
      id="score"
      className="relative flex items-cente rounded-2xl p-4 gap-1 justify-center overflow-hidden bg-white"
    >
      <div
        id="lineBar"
        className={`size-4  rounded-full ${
          status === "Pass"
            ? "bg-[#03b300]"
            : status === "Fail"
            ? "bg-[#ff0000]"
            : "bg-[#494949]"
        } absolute -top-[6px] left-[50%] transform -translate-x-[50%]`}
      ></div>

      <div className="flex flex-col justify-center gap-1 items-center ">
        <div
          id="scroe"
          className="text-text-primary text-[20px]  font-bold leading-none  text-center"
        >
          {score}
        </div>
        <span className="text-text-primary/70 text-[14px] leading-none font-normal text-center">
          {label}
        </span>
      </div>
    </div>
  );
};

const ElderlyDetailsData = {
  name: "John Deo",
  address: "123 Main St, Anytown, USA",
  contactPerson: "+9952654987",
  cordinates: [45, -73],
  age: 65,
  height: "170",
  gender: "Male",
  diseases: ["Diabetes", "Hypertension"],
  allergies: ["Peanuts"],
  custom_condition: [],
  status: "normal",
};

const AllScoresData = [
  {
    status: "Compliance",
    score: 0.8,
    label: "AHI Index",
  },
  {
    status: "Not Compliance",
    score: "3h 35m",
    label: "Sleep Duration",
  },
  {
    status: "Compliance",
    score: "38%",
    label: "Deep Sleep",
  },
  {
    status: "Compliance",
    score: "0",
    label: "Bed-Exit at Night",
  },
  {
    status: "Compliance",
    score: "13/min",
    label: "Avg. Breath Rate",
  },
  {
    status: "Compliance",
    score: "68/min",
    label: "Avg. Heart Rate",
  },
];
