import { CustomContext } from "@/Context/UseCustomContext";

export default function ElderlyActivityGraph() {
  const { sleepData } = useContext(CustomContext);

  return (
    <>
      <div className="flex flex-col gap-4 rounded-2xl bg-white p-6 w-full">
        <div className="text-[24px] font-bold">Timeline</div>
        <div className="w-full">
          <TimelineGraph
            statisticalData={sleepData?.statistical_data}
            alarmEvents={sleepData?.alarm_events}
            noDataText="No Data Detected for Today"
          />
        </div>

        {/* <div id="alertOnthisAcivity" className="flex items-center gap-4 mt-4">
          <div className="text-[20px] font-bold text-nowrap">
            Alert on This Timeline:
          </div>
          <div id="alertList" className="flex gap-4 w-full overflow-x-auto">
            <AlertItem
              time="19:25"
              alertName="Fall Over Detected"
              type="critical"
            />
            <AlertItem
              time="19:25"
              alertName="Away For Too Long"
              type="warning"
            />
          </div>
        </div> */}
      </div>
    </>
  );
}

const AlertItem = ({ time, alertName, type }) => {
  return (
    <div
      className={`flex ${
        type === "critical" ? "bg-red-500" : "bg-yellow-500"
      } w-fit p-2 px-3 items-center gap-2 rounded-xl`}
    >
      <span className="text-[14px] p-2 py-1 font-bold bg-white rounded-full text-primary">
        {time}
      </span>
      <span className="text-[16px] font-semibold text-white text-nowrap">
        {alertName}
      </span>
    </div>
  );
};

