import React from "react";

export default function RoomMapList({ activeKey, onTabClick, tabs = [] }) {
  return (
    <>
      <div className="flex flex-col items-center justify-start gap-4 w-full !border-none  overflow-y-auto ">
        {tabs?.map((tab) => (
          <div
            key={tab._id}
            className={`tab-button flex items-center rounded-xl w-full gap-2 p-4 cursor-pointer border-2 border-transparent !h-[160px] relative ${
              activeKey === tab._id.toString()
                ? "activeTab border-2 !border-[#43B981]"
                : "bg-black/5"
            }`}
            onClick={() => onTabClick(tab._id.toString())}
          >
            {/* {tab.roomName} */}
            <div
              id="roomNmaeAndStatus"
              className="flex items-center gap-2 w-fit bg-white pr-4 py-1.5 border border-gray-200 p-2 rounded-full absolute bottom-2 left-2"
            >
              <div
                id="dot"
                className="size-3 rounded-full"
                style={{
                  backgroundColor:
                    tab.is_device_bind === true ? "green" : "red",
                }}
              ></div>
              <p>{tab.name}</p>
            </div>
          </div>
        ))}
      </div>
    </>
  );
}
