import React, { useState, useEffect, useContext } from "react";
import { LuHeartPulse } from "react-icons/lu";
import { RiLungsLine } from "react-icons/ri";
import LiveRateChart from "./LiveRateChart";
import { decodeHeartBreath } from "@/utils/helper";
import { CustomContext } from "@/Context/UseCustomContext";

export default function LiveHeartAndBreathRate() {
  const { elderlyDetails } = useContext(CustomContext);
  const deviceCode = elderlyDetails?.deviceId;

  const [data, setData] = useState({
    time: [],
    heartRate: [],
    breathRate: [],
  });

  const [liveRateData, setLiveRateData] = useState(null);
  const [lastUpdate, setLastUpdate] = useState(Date.now());

  // Decode action handler
  const handleDecode = (base64Input) => {
    const data = decodeHeartBreath(base64Input);
    setLiveRateData(data);
    setLastUpdate(Date.now());
  };

  useEffect(() => {
    const interval = setInterval(() => {
      const now = Date.now();
      const isStale = now - lastUpdate > 5000; // Data is stale if older than 5 seconds

      if (isStale) {
        setLiveRateData(null);
        setData({ time: [], heartRate: [], breathRate: [] }); // Reset to empty arrays, not just an empty array
      } else if (liveRateData) {
        const newTime = new Date().toLocaleTimeString();

        // Update the data state with liveRateData values
        setData((prevData) => ({
          time: [...prevData.time, newTime].slice(-30),
          heartRate: [...prevData.heartRate, liveRateData.heartRateValue].slice(
            -30
          ),
          breathRate: [
            ...prevData.breathRate,
            liveRateData.breathingValue,
          ].slice(-30),
        }));
      }
    }, 1000);

    return () => clearInterval(interval);
  }, [liveRateData, lastUpdate]);

  useEffect(() => {
    const ws = new WebSocket("wss://live.elderlycareplatform.com/ws/");
    ws.onopen = () => {
      console.log("Connected to WebSocket server");
    };
    ws.onmessage = (event) => {
      const data = JSON.parse(event?.data);
      const payload = JSON.parse(data?.message)?.payload;
      if (payload?.deviceCode === deviceCode && payload?.heartbreath) {
        handleDecode(payload.heartbreath);
      }
    };
    ws.onerror = (error) => {
      // console.error("WebSocket error:", error);
    };
    ws.onclose = () => {
      console.log("Disconnected from WebSocket server");
    };
    return () => ws.close();
  }, [deviceCode]);

  return (
    <div className="w-full flex gap-4">
      <div className="w-full flex flex-col bg-[#6e77eb] pb-0 gap-4 justify-between rounded-xl pt-2">
        <div className="flex items-center justify-between px-4 text-white">
          <p className="text-base font-medium flex items-center gap-2">
            <LuHeartPulse size={20} /> Realtime Heart Rate
          </p>
          <h1 className="text-2xl font-bold">
            {liveRateData?.heartRateValue ?? "--"}{" "}
            <span className="text-sm text-white/70 font-normal">/BPM</span>
          </h1>
        </div>
        <LiveRateChart data={data} />
      </div>
      <div className="w-full flex flex-col bg-[#32c1c1] pb-0 gap-4 justify-between rounded-xl pt-2">
        <div className="flex items-center justify-between px-4 text-white">
          <p className="text-base font-medium flex items-center gap-2">
            <RiLungsLine size={20} /> Realtime Breath Rate{" "}
          </p>
          <h1 className="text-2xl font-bold">
            {liveRateData?.breathingValue ?? "--"}{" "}
            <span className="text-sm text-white/70 font-normal">/BPM</span>
          </h1>
        </div>
        <LiveRateChart data={data} />
      </div>
    </div>
  );
}
